import { useContext, useEffect, useState } from "react";
import { Col, Row } from "react-bootstrap";
import Select from "react-select";
import { MongoContext } from "../context/mongo-context";
import { ARTISTS, CATEGORY } from "./filter";

function FilterPanel(props) {
  const { user, filters, setFilters } = useContext(MongoContext);
  const onChangeFilters = (key, value) => {
    let copy = new Map(filters);
    copy.set(
      key,
      value.map((v) => v.value)
    );
    console.log(copy);
    setFilters(copy);
  };
  return (
    <Row className="py-1">
      <Col xs={12} className="mb-3">
        <FilterBox
          isMulti
          filterKey="artist_name"
          label="Artists"
          fallBack={ARTISTS.map((v) => ({ value: v, label: v }))}
          onChange={(v) => onChangeFilters("artist_name", v)}
        />
      </Col>
      <Col xs={12} className="mb-3">
        <FilterBox
          isMulti
          filterKey="category"
          label="Art Category"
          onChange={(v) => onChangeFilters("category", v)}
          fallBack={CATEGORY.map((v) => ({ value: v, label: v }))}
        />
      </Col>
      <Col xs={12} className="mb-3">
        <FilterBox
          isMulti
          filterKey="auction_name"
          label="Auction"
          onChange={(v) => onChangeFilters("auction_name", v)}
        />
      </Col>
      <Col xs={12} className="mb-3">
        <FilterBox
          isMulti
          filterKey="auction_house"
          label="Auction House"
          onChange={(v) => onChangeFilters("auction_house", v)}
        />
      </Col>
    </Row>
  );
}

export default FilterPanel;

function FilterBox(props) {
  const [loading, setLoading] = useState(true);
  const [options, setOptions] = useState(props.fallBack ?? []);

  const { user } = useContext(MongoContext);

  useEffect(() => {
    setLoading(true);
    if (!props.fallBack) {
      console.log("loading");
      user.functions
        .getSaffronArtFilters(props.filterKey)
        .then((resp) => {
          console.log(resp.result);
          setOptions(resp.result.map((v) => ({ value: v, label: v })));
          setLoading(false);
        })
        .catch((err) => {
          console.log(err);
          setLoading(false);
        })
        .finally((x) => console.log(x));
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [user]);

  return (
    <>
      <span className="text-white">{props.label}</span>
      <Select
        isMulti
        name={props.filterKey}
        title={props.filterKey}
        options={options}
        onChange={props.onChange}
      />
    </>
  );
}
