import localForage from "localforage";
import { createContext, useState } from "react";
import * as Realm from "realm-web";
const APP_ID = "artcomp-github-cwvac";
export const MongoContext = createContext(null);

export const MongoContextProvider = ({ children, app: mongoRealm }) => {
  const [user, setUser] = useState();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState(".....");
  const [filters, setFilters] = useState(new Map());

  const onSubmit = (e) => {
    e.preventDefault();
    try {
      const credentials = Realm.Credentials.emailPassword(email, password);
      mongoRealm
        .logIn(credentials)
        .then((u) => (u ? setUser(u) : setUser("")))
        .catch((err) => {
          console.log(err);
          mongoRealm.emailPasswordAuth
            .registerUser({ email, password })
            .then(() => {
              const c = Realm.Credentials.emailPassword(email, password);
              mongoRealm.logIn(c).then((u) => (u ? setUser(u) : setUser("")));
            });
        });
    } catch (err) {
      console.error("Failed to log in", err);
    }
  };

  const addToImageMap = (id, value) => {
    localForage.setItem(id, value).catch(function (err) {
      console.log(err);
    });
  };

  const getImageData = (id) => {
    localForage
      .getItem(id)
      .then(function (value) {
        return value;
      })
      .catch(function (err) {
        console.log(err);
      });
  };

  return (
    <MongoContext.Provider
      value={{
        id: APP_ID,
        user,
        getImageData,
        addToImageMap,
        filters,
        setFilters,
        email,
        password,
        setEmail,
        setPassword,
        onSubmit,
      }}
    >
      {children}
    </MongoContext.Provider>
  );
};
