import { Row } from "react-bootstrap";
import Container from "react-bootstrap/Container";
import Navbar from "react-bootstrap/Navbar";
import * as Realm from "realm-web";
import "./App.css";
import ArtWorks from "./components/art-works";
import { MongoContextProvider } from "./context/mongo-context";
import logo from "./logo.svg";

const APP_ID = "artcomp-github-cwvac";

const mongoRealm = new Realm.App({ id: APP_ID });

function App() {
  const signOut = () => {
    mongoRealm.currentUser.logOut();
  };

  return (
    <Container fluid className="app-page bg-dark">
      <MongoContextProvider app={mongoRealm}>
        <Navbar expand="lg" bg="dark" variant="dark">
          <Container className="font-monospace m-1 p-2 d-flex">
            <span>
              <img src={logo} alt="art logo" />
              <span className="text-white p-1">Art works</span>
            </span>
            {/* <Button
              onClick={() => signOut().then((x) => console.log(x))}
              variant="outline"
              className="p-0"
              size="sm"
            >
              <FontAwesomeIcon icon={faSignOut} size="sm" color="grey" />
            </Button> */}
          </Container>
        </Navbar>
        <Row className="justify-content-md-center">
          <ArtWorks />
        </Row>
      </MongoContextProvider>
    </Container>
  );
}

export default App;
